ss:"row mb-5"},[_c('div',{staticClass:"col-lg-4 mb-3 mb-lg-0"},[_c('figure',[_c('img',{attrs:{"src":require("@/assets/curso/tema3/10.svg"),"data-aos":"zoom-in"}})])]),_c('div',{staticClass:"col-lg-8"},[_c('p',{attrs:{"data-aos":"fade-left"}},[_vm._v("La optimización para dispositivos móviles requiere una comprensión profunda de las características únicas de esta plataforma, incluyendo limitaciones de ancho de banda, métodos de interacción táctil, contextos de uso diversos, y expectativas específicas de rendimiento. El diseño móvil no es simplemente una versión reducida del diseño de desktop, sino una experiencia completamente diferente que debe considerar factores como el tamaño de los elementos táctiles, la jerarquía visual en espacios limitados, y la optimización para conexiones de red variables. Los usuarios móviles a menudo buscan información específica rápidamente, requiriendo interfaces que prioricen contenido esencial y minimicen la fricción en las interacciones más comunes.")]),_c('div',{staticClass:"bg-color-6 p-5",attrs:{"data-aos":"fade-left"}},[_c('div',{staticClass:"row align-items-start"},[_c('div',{staticClass:"col-lg-auto"},[_c('img',{staticClass:"mx-auto",staticStyle:{"max-width":"90px"},attrs:{"src":require("@/assets/curso/tema3/11.svg")}})]),_c('div',{staticClass:"col-lg"},[_c('p',{staticClass:"mb-0"},[_vm._v("Celaya Luna (2015), destaca que las mejores prácticas de UX móvil incluyen la implementación de navegación simplificada, optimización de formularios para entrada táctil, y la reducción de la carga cognitiva mediante interfaces intuitivas que aprovechan patrones de interacción familiares. Se implementan técnicas como lazy loading para imágenes, compresión de recursos para reducir tiempos de carga, y Progressive Web App (PWA) features que proporcionan experiencias similares a aplicaciones nativas. La optimización móvil también incluye consideraciones de accesibilidad específicas, como garantizar que los elementos táctiles tengan tamaños mínimos apropiados y que el contraste sea suficiente para visualización en diferentes condiciones de iluminación.")])])])])])])},function () {var _vm=this;var _h=_vm.$createElement;var _c=_vm._self._c||_h;return _c('div',{staticClass:"row mb-5"},[_c('div',{staticClass:"col-lg-4 order-lg-2 mb-3 mb-lg-0"},[_c('figure',[_c('img',{attrs:{"src":require("@/assets/curso/tema3/12.png"),"data-aos":"zoom-in"}})])]),_c('div',{staticClass:"col-lg-8 order-lg-1"},[_c('p',{attrs:{"data-aos":"fade-left"}},[_vm._v("En el contexto de herramientas para equipos de ingeniería de software, se observa la necesidad de optimizar interfaces móviles para casos de uso específicos como revisión de código durante desplazamientos, acceso a documentación técnica en dispositivos móviles, y notificaciones de sistemas de CI/CD que requieren acción inmediata. Los desarrolladores necesitan poder realizar tareas críticas desde dispositivos móviles, como aprobar pull requests, revisar logs de error, y comunicarse con el equipo sobre solicitudes urgentes. Esta funcionalidad requiere interfaces que simplifiquen tareas complejas sin sacrificar la funcionalidad esencial.")]),_c('div',{staticClass:"bg-color-2 p-5",attrs:{"data-aos":"fade-left"}},[_c('p',{staticClass:"mb-0"},[_vm._v("Para demostrar las mejores prácticas de UX móvil, se propone crear una aplicación de gestión de tareas de desarrollo optimizada específicamente para dispositivos móviles. Se implementa una interfaz que utilice gestos táctiles intuitivos como swipe para marcar tareas completadas, pull-to-refresh para actualizar el estado del proyecto, y tap-and-hold para acceder a opciones avanzadas. Se optimiza la navegación utilizando un patrón de tab bar que permita acceso rápido a las secciones más importantes: tareas pendientes, notificaciones, y comunicación del equipo. Se añaden características específicas móviles como notificaciones push simuladas, modo offline para visualizar información crítica sin conexión, e integración con características del dispositivo como cámara para adjuntar imágenes a los reportes de bugs. El resultado demuestra cómo las consideraciones específicas de UX móvil pueden crear experiencias que no solo funcionan en dispositivos móviles, sino que aprovechan las características únicas de esta plataforma.")])])])])},function () {var _vm=this;var _h=_vm.$createElement;var _c=_vm._self._c||_h;return _c('div',{staticClass:"titulo-segundo",attrs:{"id":"t_3_5","data-aos":"flip-up"}},[_c('h2',[_c('span',[_vm._v("3.5")]),_vm._v(" Accesibilidad web: implementación de un diseño inclusivo y accesible")])])},function () {var _vm=this;var _h=_vm.$createElement;var _c=_vm._self._c||_h;return _c('div',{staticClass:"anexo__texto"},[_c('p',[_c('strong',[_vm._v("Anexo. ")]),_vm._v(" Accesibilidad web: implementación de un diseño inclusivo y accesible ")])])},function () {var _vm=this;var _h=_vm.$createElement;var _c=_vm._self._c||_h;return _c('div',{staticClass:"col-lg-4 mb-3 mb-lg-0 order-1"},[_c('figure',{attrs:{"data-aos":"zoom-in"}},[_c('img',{attrs:{"src":require("@/assets/curso/tema3/13.png"),"alt":""}})])])},function () {var _vm=this;var _h=_vm.$createElement;var _c=_vm._self._c||_h;return _c('div',{staticClass:"row align-items-center mb-5"},[_c('div',{staticClass:"col-lg-auto mb-3 mb-lg-0"},[_c('img',{staticClass:"mx-auto",staticStyle:{"max-width":"90px"},attrs:{"src":require("@/assets/curso/tema3/14.svg")}})]),_c('div',{staticClass:"col-lg"},[_c('h2',{attrs:{"data-aos":"fade-left"}},[_vm._v("Reflexión")])])])},function () {var _vm=this;var _h=_vm.$createElement;var _c=_vm._self._c||_h;return _c('div',{staticClass:"bg-full-width bg-color-7 mb-5"},[_c('div',{staticClass:"px-4 px-md-5 py-4"},[_c('div',{staticClass:"row align-items-center"},[_c('div',{staticClass:"col-lg-auto"},[_c('img',{staticClass:"mx-auto",staticStyle:{"max-width":"90px"},attrs:{"src":require("@/assets/curso/tema3/18.svg")}})]),_c('div',{staticClass:"col-lg text-white"},[_c('p',{staticClass:"mb-0"},[_vm._v("La aplicación de esto